<template>
  <el-form ref="ruleFormRef" :model="ruleForm" :rules="rules">
    <el-form-item
      v-for="key in Object.keys(ruleForm)"
      v-bind:key="key"
      class="ahab-form-item"
      :class="formConfig[key]?.withButton ? 'ahab-form-item-width-button' : ''"
      :prop="key"
    >
      <el-input
        class="ahab-input"
        :type="formConfig[key]?.type || 'text'"
        v-model="ruleForm[key]"
        :placeholder="formConfig[key]?.placeholder"
        @blur="formConfig[key]?.onBlur ? formConfig[key]?.onBlur(ruleForm[key]) : () => ({})"
      />
      <el-button
        v-if="formConfig[key]?.withButton"
        type="primary"
        :disabled="formConfig[key]?.buttonDisabled"
        @click="formConfig[key]?.buttonClick"
      >
        {{ formConfig[key]?.buttonText }}
      </el-button>
    </el-form-item>
    <el-form-item>
      <div class="user-form-item">
        <el-button v-if="showReset" @click="resetForm(ruleFormRef)">{{ resetText }}</el-button>
        <el-button type="primary" @click="submitForm(ruleFormRef)">{{ confirmText }}</el-button>
      </div>
    </el-form-item>
    <slot name="footer"></slot>
  </el-form>
</template>
<script lang="ts" setup>
import { ref } from 'vue'
import type { ModelRef } from 'vue'
import { ElButton } from 'element-plus'
import type { FormInstance } from 'element-plus'

interface FormConfig {
  [formItem: string]: any
}

interface RuleForm {
  [formItem: string]: any
}

export interface Props {
  formConfig?: FormConfig
  rules: any
  confirmText: string
  resetText: string
  showReset: boolean
}

const ruleForm = defineModel('ruleForm') as ModelRef<RuleForm>

withDefaults(defineProps<Props>(), {
  formConfig: () => ({}),
  rules: [],
  confirmText: 'confirm',
  resetText: 'reset',
  showReset: false,
})

const emits = defineEmits(['submit'])

const ruleFormRef = ref<FormInstance>()

const submitForm = (formEl: FormInstance | undefined) => {
  if (!formEl) return
  try {
    formEl.validate((valid, fields) => {
      if (valid) {
        console.log('submit!')
        emits('submit')
      } else {
        console.log('error submit!', fields)
      }
    })
  } catch (error) {
    console.log(error)
  }
}

const resetForm = (formEl: FormInstance | undefined) => {
  if (!formEl) return
  formEl.resetFields()
}
</script>
<style lang="scss" scoped>
@use '@/assets/scss/user/index.scss';
.ahab-form-item {
  margin-bottom: 20px;
  &-width-button {
    .el-form-item__content {
      display: flex;
      .el-input {
        flex: 1;
        margin-right: 10px;
      }
    }
  }
}
</style>
